<template lang="html">
  <div>
    <Select v-model="currOperatable" style="width:200px" placeholder="" @on-change="changeType()">
      <Option v-for="(value, key) in operatable_type" :value="key" :key="key">{{ value.label }}</Option>
    </Select>
    <!-- <Button type="primary" @click="search()"></Button> -->
    <Page :total="paging.total" :page-size="paging.pre_page" simple @on-change="changePage($event)"></Page>
    <Table stripe :columns="columns1" :data="eventsLog"></Table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currOperatable: '2',
      operatable_type: {
        '2': {
          label: '',
          sign: 'DragonColumns',
        },
        '3': {
          label: '',
          sign: 'TreeColumns',
        },
      },
      actionType: [
        '',
        '',
        '',
        '',
        '',
      ],
      TreeColumns: [
        {
          title: '',
          key: 'created_at',
          minWidth: 150,
        },
        {
          title: '',
          key: 'operator_name',
          minWidth: 100,
        },
        {
          title: '',
          key: 'action',
          width: 120,
        },
        {
          title: '',
          key: 'item',
          width: 100,
        },
        // {
        //   title: '',
        //   key: 'remain',
        //   minWidth: 120,
        // },
        // {
        //   title: '',
        //   key: 'capacity',
        //   minWidth: 120,
        // },
        // {
        //   title: '',
        //   key: 'progress',
        //   minWidth: 120,
        // },
        {
          title: '',
          key: 'user_name',
          minWidth: 100,
        },
      ],
      DragonColumns: [
        {
          title: '',
          key: 'created_at',
          minWidth: 150,
        },
        {
          title: '',
          key: 'operator_name',
          minWidth: 100,
        },
        {
          title: '',
          key: 'action',
          width: 120,
        },
        {
          title: '',
          key: 'item',
          width: 100,
        },
        // {
        //   title: '',
        //   key: 'remain',
        //   minWidth: 120,
        // },
        // {
        //   title: '',
        //   key: 'capacity',
        //   minWidth: 120,
        // },
        // {
        //   title: '',
        //   key: 'progress',
        //   minWidth: 120,
        // },
        {
          title: '',
          key: 'user_name',
          minWidth: 100,
        },
      ],
    }
  },
  computed: {
    eventsLog () {
      return this.$store.getters.eventsLog.data.map(item => {
        item.action = `${this.actionType[item.type]}`
        item.item = `${this.operatable_type[item.operatable_type].label}`
        if (item.operatable_type === 2 && item.result_data.type === 1) {
          item.item = ''
        }
        item.operator_name = Object.assign({name: ''}, item.operator).name
        // item.operator_name = item.operator.name || ''
        item.user_name = item.user.name
        // item.remain = item.result_data.remain
        // item.capacity = item.result_data.capacity
        // item.progress = item.result_data.progress
        return item
      })
    },
    paging () {
      return this.$store.getters.paging('user', 'eventsLog')
    },
    columns1 () {
      return this[`${this.operatable_type[this.currOperatable].sign}`]
    },
  },
  methods: {
    async changePage (nextIndex) {
      const searchParams = new URLSearchParams()
      searchParams.append('operatable_type', this.currOperatable)
      searchParams.append('type[]', `0`)
      searchParams.append('type[]', `2`)
      await this.$store.dispatch('EventsLog', { nextIndex, searchParams })
    },
    async changeType () {
      const searchParams = new URLSearchParams()
      searchParams.append('operatable_type', this.currOperatable)
      const nextIndex = `1` // this.$store.getters.paging('user', 'eventsLog').curr_page
      await this.$store.dispatch('EventsLog', { nextIndex, searchParams })
    },
  },
}
</script>

<style lang="css">
</style>



